body {
  background-color: darkcyan;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.btn1 img {
  width: 17px;
  background-color: transparent;
  padding-top: 2px;
  padding-right: 5px;
}

.btn1 {
  margin-right: 30px;
  /* font-size: x-large; */
  border-radius: 10px;
  border: 1px solid rgb(181, 179, 179);
  padding: 5px;
  background-color: transparent;
  padding: 5px 15px 5px 15px;
  font-size: 21px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

input {
  display: flex;
  border: none;
  border-bottom: 1px solid rgb(245, 237, 237);
  font-size: 20px;
  width: 70%;
  margin: 50px 0;
}

.cont {
  display: flex;
  position: relative;
}

.top h1 {
  margin-bottom: 30px;
}

.cont2 {
  padding-top: 20px;
  background-color: white;
  border-radius: 50px 0 0 50px;
  width: 100%;
  margin-left: 520px;
  height: 711px;
  margin-top: -10px;
  margin-right: -35px;
  margin-bottom: -10px;
}

.img-div img {
  margin: 50px;
  position: absolute;
  top: 100px;
  left: 60px;
}

input:hover {
  border: 1px solid lightgrey;
}

.btn2 {
  width: 70%;
  margin-bottom: 30px;
  margin-right: 30px;
  font-size: x-large;
  border-radius: 10px;
  border: 1px solid gray;
  background-color: darkcyan;
  padding: 6px;
  color: white;
  font-weight: bold;
}

.password-input {
  position: relative;
}

button:hover {
  cursor: pointer;
}

.password-toggle {
  position: absolute;
  top: 50%;
  right: 260px;
  transform: translateY(-50%);
  cursor: pointer;
}

.error-text {
  color: red;
  font-size: larger;
}

.success-text {
  color: green;
  font-size: larger;
}

.or {
  margin-left: 240px;
  margin-top: 30px;
  color: gray;
}

.top,
.form {
  margin-left: 150px;
}
